body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

body {
  min-height: 100vh;
  -webkit-transition: all .2s;
  transition: all .2s;
}

body * {
  -webkit-transition: all .2s;
  transition: all .2s;
}
#options-menu .menu-item {
  text-decoration: none;
  padding: 10px 0;
  font-size: 22px;
}

#options-menu {
  display: block;
  width: 50%;
  position: relative;
  top: 50px;
  left: 50px;
  z-index: 1;
  -webkit-user-select: none;
  -moz-user-select: none;
   -ms-user-select: none;
       user-select: none;
}

#options-menu #options-toggle {
  display: block;
  width: 40px;
  height: 32px;
  position: absolute;
  top: -7px;
  left: -5px;
  cursor: pointer;
  opacity: 0;
  z-index: 2;
  -webkit-touch-callout: none;
}

#options-menu .hamburger-slide {
  display: block;
  width: 33px;
  height: 4px;
  margin-bottom: 5px;
  position: relative;
  border-radius: 3px;
  z-index: 1;
  -webkit-transform-origin: 4px 0px;
          transform-origin: 4px 0px;
  -webkit-transition: opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease;
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), opacity 0.55s ease, -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

#options-menu .hamburger-slide:first-child {
  -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
}

#options-menu .hamburger-slide:nth-last-child(2) {
  -webkit-transform-origin: 0% 100%;
          transform-origin: 0% 100%;
}

#options-menu #options-toggle:checked ~ .hamburger-slide {
  opacity: 1;
  -webkit-transform: rotate(45deg) translate(-2px, -1px);
          transform: rotate(45deg) translate(-2px, -1px);
}

#options-menu #options-toggle:checked ~ .hamburger-slide:nth-last-child(3) {
  opacity: 0;
  -webkit-transform: rotate(0deg) scale(0.2, 0.2);
          transform: rotate(0deg) scale(0.2, 0.2);
}

#options-menu #options-toggle:checked ~ .hamburger-slide:nth-last-child(2) {
  -webkit-transform: rotate(-45deg) translate(0, -1px);
          transform: rotate(-45deg) translate(0, -1px);
}

#options-menu #menu {
  position: absolute;
  width: 350px;
  margin: -100px 0 0 -50px;
  padding: 50px;
  padding-top: 125px;
  border-right: 1px solid #999;
  border-bottom: 1px solid #999;
  list-style-type: none;
  -webkit-font-smoothing: antialiased;
  -webkit-transform-origin: 0% 0%;
          transform-origin: 0% 0%;
  -webkit-transform: translate(-100%, 0);
          transform: translate(-100%, 0);
  -webkit-transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
  transition: transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1), -webkit-transform 0.5s cubic-bezier(0.77, 0.2, 0.05, 1);
}

#options-menu #options-toggle:checked ~ ul {
  -webkit-transform: none;
          transform: none;
}

@media only screen and (max-width: 500px) {
  #options-menu #menu {
    width: 255px;
  }

  #options-menu .menu-item {
    font-size: 17px;
  }
}

.panel-size-slider {
  display: flex;
  justify-content: space-between;
}

.panel-size-slider input[type=range] {
  -webkit-appearance: none; /* Hides the slider so that custom slider can be made */
  width: 130px; /* Specific width is required for Firefox. */
  background: transparent; /* Otherwise white in Chrome */
}

.panel-size-slider input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
}

.panel-size-slider input[type=range]:focus {
  outline: none; /* Removes the blue border. You should probably do some kind of focus styling for accessibility reasons though. */
}

.panel-size-slider input[type=range]::-ms-track {
  width: 100%;
  cursor: pointer;

  /* Hides the slider so custom styles can be added */
  background: transparent; 
  border-color: transparent;
  color: transparent;
}

/* Special styling for WebKit/Blink */
.panel-size-slider input[type=range]::-webkit-slider-thumb {
  -webkit-appearance: none;
  border: 1px solid #000000;
  height: 30px;
  width: 9px;
  border-radius: 3px;
  background: #006eff;
  cursor: pointer;
  margin-top: -14px; /* You need to specify a margin in Chrome, but in Firefox and IE it is automatic */
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d; /* Add cool effects to your sliders! */
}

/* All the same stuff for Firefox */
.panel-size-slider input[type=range]::-moz-range-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 30px;
  width: 9px;
  border-radius: 3px;
  background: #006eff;
  cursor: pointer;
}

/* All the same stuff for IE */
.panel-size-slider input[type=range]::-ms-thumb {
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  border: 1px solid #000000;
  height: 30px;
  width: 9px;
  border-radius: 3px;
  background: #006eff;
  cursor: pointer;
}

.panel-size-slider input[type=range]::-webkit-slider-runnable-track {
  width: 100%;
  height: 8px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: #eee;
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}

.panel-size-slider input[type=range]:focus::-webkit-slider-runnable-track {
  background: #eee;
}

.panel-size-slider input[type=range]::-moz-range-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
  background: #eee;
  border-radius: 1.3px;
  border: 0.2px solid #010101;
}

.panel-size-slider input[type=range]::-ms-track {
  width: 100%;
  height: 8.4px;
  cursor: pointer;
  background: transparent;
  border-color: transparent;
  border-width: 16px 0;
  color: transparent;
}
.panel-size-slider input[type=range]::-ms-fill-lower {
  background: #eee;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
.panel-size-slider input[type=range]:focus::-ms-fill-lower {
  background: #eee;
}
.panel-size-slider input[type=range]::-ms-fill-upper {
  background: #eee;
  border: 0.2px solid #010101;
  border-radius: 2.6px;
  box-shadow: 1px 1px 1px #000000, 0px 0px 1px #0d0d0d;
}
.panel-size-slider input[type=range]:focus::-ms-fill-upper {
  background: #eee;
}
