#theme-switch {
  position: absolute;
  right: 40px;
  top: 50px;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.switch-slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 34px;
}

.switch-slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: #333;
  -webkit-transition: .4s;
  transition: .4s;
  border-radius: 50%;
}

input:checked + .switch-slider {
  background-color: #333;
}

input:checked + .switch-slider:before {
  -webkit-transform: translateX(26px);
  transform: translateX(26px);
  background-color: #f5f5f5;
}

@media only screen and (max-width: 700px) {
  #theme-switch {
    left: 50%;
    right: auto;
    top: 110px;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
  }
}

